@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap');
@import './vars';

*,
body,
html {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: black;
  overflow: hidden;
}

#bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f3f3f3;
  z-index: -10;
}

h1 {
  font-size: 3rem;
}
a {
  text-decoration: none;
}

.mt {
  margin-top: 1rem;
}

.mt-big {
  margin-top: 2.5rem;
}

.mb {
  margin-bottom: 1rem;
}
.mb-big {
  margin-bottom: 2.5rem;
}

.container {
  width: 80%;
  max-width: 80%;
  margin: 0 auto;
  padding: 1rem;
}

.text-center {
  text-align: center;
}
